@import "@rubygems/proscenium-ui/config/props.css";
pui-field {
display: block;
[part="error"] {
&::before {
content: " ";
}
color: red;
}
[part="hint"] {
font-size: 0.9\65 m;
margin-top: 0.2\65 m;
}
input {
border: var(--pui-input-border);
border-radius: var(--pui-input-border-radius);
background-color: var(--pui-input-background-color);
}
}
pui-checkbox {
display: block;
label {
display: flex;
gap: 0.3rem;
}
}
pui-radio-group {
[part="radio_group_inputs"] {
display: flex;
gap: 1\65 m;
> label > input {
margin-right: 0.3\65 m;
}
}
}
pui-textarea {
display: block;
textarea {
display: block;
}
}
